<script>
import careModeMixin from '@/mixins/careMode.js'

export default {
  mixins: [careModeMixin],
  data() {
    return {
      statusBarHeight: '40px',
      safeAreaBottom: '40rpx',
      userInfo: {},
      formData: {
        nickName: '',
        name: '',
        age: '',
        gender: '',
        userType: '',
        signature: '',
        avatarUrl: ''
      },
      saving: false
    }
  },
  onLoad() {
    const sys = uni.getSystemInfoSync();
    this.statusBarHeight = sys.statusBarHeight ? sys.statusBarHeight + 'px' : '40px';
    
    // Ëé∑ÂèñÂÆâÂÖ®Âå∫ÂüüÂ∫ïÈÉ®È´òÂ∫¶
    if (sys.safeAreaInsets) {
      this.safeAreaBottom = (sys.safeAreaInsets.bottom || 0) + 40 + 'rpx';
    }
    
    this.loadUserInfo();
  },
  methods: {
    // Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
    loadUserInfo() {
      const userInfo = uni.getStorageSync('userInfo');
      this.userInfo = userInfo || {};
      
      // Â°´ÂÖÖË°®ÂçïÊï∞ÊçÆ
      this.formData = {
        nickName: this.userInfo.nickName || '',
        name: this.userInfo.name || '',
        age: this.userInfo.age || '',
        gender: this.userInfo.gender || '',
        userType: this.userInfo.userType || '',
        signature: this.userInfo.signature || '',
        avatarUrl: this.userInfo.avatarUrl || ''
      };
    },
    
    // ËøîÂõû‰∏ä‰∏ÄÈ°µ
    goBack() {
      uni.navigateBack();
    },
    
    // Êõ¥Êç¢Â§¥ÂÉè
    changeAvatar() {
      uni.showActionSheet({
        itemList: ['ÊãçÁÖß', '‰ªéÁõ∏ÂÜåÈÄâÊã©'],
        success: (res) => {
          if (res.tapIndex === 0) {
            this.takePhoto();
          } else if (res.tapIndex === 1) {
            this.chooseImage();
          }
        }
      });
    },
    
    // ÊãçÁÖß
    takePhoto() {
      uni.chooseImage({
        count: 1,
        sourceType: ['camera'],
        success: (res) => {
          this.uploadAvatar(res.tempFilePaths[0]);
        },
        fail: (err) => {
          console.error('ÊãçÁÖßÂ§±Ë¥•:', err);
          uni.showToast({
            title: 'ÊãçÁÖßÂ§±Ë¥•',
            icon: 'error'
          });
        }
      });
    },
    
    // ‰ªéÁõ∏ÂÜåÈÄâÊã©
    chooseImage() {
      uni.chooseImage({
        count: 1,
        sourceType: ['album'],
        success: (res) => {
          this.uploadAvatar(res.tempFilePaths[0]);
        },
        fail: (err) => {
          console.error('ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•:', err);
          uni.showToast({
            title: 'ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•',
            icon: 'error'
          });
        }
      });
    },
    
    // ‰∏ä‰º†Â§¥ÂÉè
    uploadAvatar(imagePath) {
      uni.showLoading({
        title: 'Â§ÑÁêÜ‰∏≠...'
      });
      
      // Ê®°Êãü‰∏ä‰º†ËøáÁ®ã
      setTimeout(() => {
        // ‰øùÂ≠òÂ§¥ÂÉèË∑ØÂæÑÂà∞Ë°®ÂçïÊï∞ÊçÆ
        this.formData.avatarUrl = imagePath;
        
        uni.hideLoading();
        uni.showToast({
          title: 'Â§¥ÂÉèÂ∑≤ÈÄâÊã©',
          icon: 'success'
        });
      }, 500);
    },
    
    // Â§ÑÁêÜÂ§¥ÂÉèÂä†ËΩΩÈîôËØØ
    handleAvatarError() {
      console.log('Â§¥ÂÉèÂä†ËΩΩÂ§±Ë¥•Ôºå‰ΩøÁî®ÈªòËÆ§Â§¥ÂÉè');
      this.formData.avatarUrl = '';
    },
    
    
    // ÈÄâÊã©ÊÄßÂà´
    selectGender(gender) {
      this.formData.gender = gender;
    },
    
    // ÈÄâÊã©Áî®Êà∑Á±ªÂûã
    selectUserType(userType) {
      this.formData.userType = userType;
    },
    
    // ‰øùÂ≠òËµÑÊñô
    saveProfile() {
      if (this.saving) return;
      
      // È™åËØÅÂøÖÂ°´È°π
      if (!this.formData.nickName.trim()) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•ÊòµÁß∞', icon: 'none' });
        return;
      }
      
      // È™åËØÅÂπ¥ÈæÑ
      if (this.formData.age && (isNaN(this.formData.age) || this.formData.age < 1 || this.formData.age > 120)) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•ÊúâÊïàÂπ¥ÈæÑ', icon: 'none' });
        return;
      }
      
      this.saving = true;
      
      // Êõ¥Êñ∞Áî®Êà∑‰ø°ÊÅØ
      const updatedUserInfo = {
        ...this.userInfo,
        nickName: this.formData.nickName.trim(),
        name: this.formData.name.trim(),
        age: this.formData.age,
        gender: this.formData.gender,
        userType: this.formData.userType,
        signature: this.formData.signature.trim(),
        avatarUrl: this.formData.avatarUrl
      };
      
      // ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
      uni.setStorageSync('userInfo', updatedUserInfo);
      
      // ÊòæÁ§∫ÊàêÂäüÊèêÁ§∫
      uni.showToast({ 
        title: '‰øùÂ≠òÊàêÂäü', 
        icon: 'success',
        duration: 1500
      });
      
      // Âª∂ËøüËøîÂõûÔºåËÆ©Áî®Êà∑ÁúãÂà∞ÊàêÂäüÊèêÁ§∫
      setTimeout(() => {
        this.saving = false;
        uni.navigateBack();
      }, 1500);
    }
  }
}
</script>

